li.list-group-item{
    cursor: pointer;
    transition: 3s;
}

.image-search{
    height: 50px;
    width: 50px;
    border-radius: 2em;
}

.search-user-name{
    margin-left: 15px;
    vertical-align: top;
    line-height: 40px;
}

.search-icon{
    margin-top: 15px ;
    float: right;
}

.search-container{
    z-index: 1;
    background: transparent;
    position: fixed;
    margin-left: 25%;
    top: 50px;
}

.search-box{
    
    background: transparent;
    padding: 0;
}